@import url(https://fonts.googleapis.com/css?family=Caveat|Gothic+A1|Kaushan+Script&display=swap);
@import url(https://fonts.googleapis.com/css?family=Calistoga|Playfair+Display&display=swap);
@import url(https://fonts.googleapis.com/css?family=Rubik+Mono+One&display=swap);

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}
/* header */
.Header {
  background-color: black;
  left: 0;
  bottom: 0;
  width: 100%;
  color: white;
  text-align: center;
}
.last {
  color: rgb(90, 168, 194);
}
/*landing lines */
#quotes {
  font-family: "Rubik Mono One", sans-serif;
  font-size: 40px;
  color: rgb(99, 13, 13);
}
#text {
  font-family: "Playfair Display", serif;
  font-size: 40px;
  color: rgb(99, 13, 13);
  float: left;
}
/* search box */
.search {
  background-color: black;
  position: relative;
  display: inline-block;
  width: 30%;
  margin-top: 10%;
}
input {
  background: rgb(54, 52, 52);
  width: 100%;
  height: 50px;
  border: none;
  /*  border: 4px solid rgb(99, 13, 13); */
  border-radius: 2%;
  padding: 10px 10px 10px 10px;
}
input:focus {
  background: rgb(54, 52, 52);
  width: 100%;
  height: 50px;
  outline: none;
}
.search-box {
  background: rgb(54, 52, 52);
  width: 100%;
  height: 50px;
  font-family: "Gothic A1", sans-serif;
  font-size: 20px;
  color: white;
}
/* suggest */
.suggest {
  display: inline-block;
  background: black;
  border-bottom: none;
  border-top: none;
  text-align: left;
  color: white;
  font-size: 20px;
  width: 100%;
}
